"use client";
import { useEffect, useState } from "react";
import HomeMenu from "@/components/home";
import CariProduk from "@/components/CariBarang";
import TambahBarang from "@/components/TambahBarang";
import EditBarang from "@/components/EditBarang";
import HapusBarang from "@/components/HapusBarang";
import Navbar from "@/components/navbar";
import DaftarBarang from "@/components/DaftarBarang";
import { supabase } from "@/lib/supabase";

type Barang = {
  kode_barang: string;
  stok: number;
  kategori: string;
  nama_barang: string;
  harga: number;
};

export default function Page() {
  const [mode, setMode] = useState<
    "home" | "cari" | "tambah" | "edit" | "hapus"
  >("home");
  const [barangList, setBarangList] = useState<Barang[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchBarang = async () => {
      const { data, error } = await supabase.from("Barang_Warput").select("*");
      if (error) {
        console.error("Gagal mengambil data:", error.message);
      } else {
        setBarangList(data as Barang[]);
      }
      setLoading(false);
    };

    fetchBarang();
  }, []);

  const goHome = () => setMode("home");

  if (mode === "cari") return <CariProduk onBack={goHome} items={barangList} />;
  if (mode === "tambah") return <TambahBarang onBack={goHome} />;
  if (mode === "edit") return <EditBarang onBack={goHome} />;
  if (mode === "hapus") return <HapusBarang onBack={goHome} />;

  return (
    <main>
      <Navbar />
      <HomeMenu
        onCari={() => setMode("cari")}
        onTambah={() => setMode("tambah")}
        onEdit={() => setMode("edit")}
        onHapus={() => setMode("hapus")}
      />
      {!loading && <DaftarBarang items={barangList} />}
    </main>
  );
}
