"use client";
import { FiSearch, FiPlus, FiEdit, FiTrash } from "react-icons/fi";
import { useState } from "react";
import { supabase } from "../lib/supabase";

type Barang = {
  kode_barang: string;
  nama_barang: string;
  kategori: string;
  stok: number;
  harga: number;
};

export default function HomeMenu({
  onCari,
  onTambah,
  onEdit,
  onHapus,
}: {
  onCari: () => void;
  onTambah: () => void;
  onEdit: () => void;
  onHapus: () => void;
}) {
  const [barangList, setBarangList] = useState<Barang[]>([]);
  const [showTable, setShowTable] = useState(false);

  const fetchBarang = async () => {
    const { data, error } = await supabase.from("Barang_Warput").select("*");
    if (error) {
      console.error("Gagal mengambil data:", error.message);
    } else {
      setBarangList(data);
      setShowTable(true);
    }
  };
  fetchBarang();
  return (
    <div className="min-h-screen bg-gray-900 text-white p-6 flex flex-col items-center">
      <div className="flex flex-wrap justify-center gap-4 mb-10">
        <ActionButton
          color="bg-black"
          icon={<FiSearch size={48} />}
          label="Cari Barang"
          onClick={onCari}
        />
        <ActionButton
          color="bg-gray-800"
          icon={<FiPlus size={48} />}
          label="Tambah Barang"
          onClick={onTambah}
        />
        <ActionButton
          color="bg-gray-600"
          icon={<FiEdit size={48} />}
          label="Edit Barang"
          onClick={onEdit}
        />
        <ActionButton
          color="bg-red-700"
          icon={<FiTrash size={48} />}
          label="Hapus Barang"
          onClick={onHapus}
        />
      </div>

      {showTable && (
        <div className="w-full max-w-5xl overflow-x-auto">
          <h2 className="text-xl font-semibold mb-4">Daftar Barang</h2>
          <table className="min-w-full bg-gray-800 text-sm rounded-lg">
            <thead>
              <tr className="bg-gray-700 text-white">
                <th className="py-3 px-4 text-left">Kode</th>
                <th className="py-3 px-4 text-left">Nama</th>
                <th className="py-3 px-4 text-left">Kategori</th>
                <th className="py-3 px-4 text-left">Stok</th>
                <th className="py-3 px-4 text-left">Harga</th>
              </tr>
            </thead>
            <tbody>
              {barangList.map((barang) => (
                <tr
                  key={barang.kode_barang}
                  className="border-b border-gray-700"
                >
                  <td className="py-3 px-4">{barang.kode_barang}</td>
                  <td className="py-3 px-4">{barang.nama_barang}</td>
                  <td className="py-3 px-4">{barang.kategori}</td>
                  <td className="py-3 px-4">{barang.stok}</td>
                  <td className="py-3 px-4">
                    Rp {barang.harga.toLocaleString()}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
}

function ActionButton({
  icon,
  label,
  color,
  onClick,
}: {
  icon: React.ReactNode;
  label: string;
  color: string;
  onClick?: () => void;
}) {
  return (
    <button
      onClick={onClick}
      className={`${color} text-white w-32 h-32 rounded-2xl flex flex-col items-center justify-center shadow-xl hover:scale-105 transition`}
    >
      {icon}
      <span className="mt-3 text-sm font-semibold text-center">{label}</span>
    </button>
  );
}
